<template>
<!-- 右上角收藏 -->
    <div class="pricePublic">
        <div class="price">
            <div v-if="type === 'rank'">
                <el-table :cell-class-name="rankingCellClassName" :data="dataList" max-height="300" :empty-text="$t('main.priceList.NoData')">
                    <el-table-column :label="$t('main.priceList.ranking')">
                        <template slot-scope="scope">
                            <p :class='{"ranking-1": ((scope.$index+1) == 1) ,"ranking-2": ((scope.$index+1) == 2),"ranking-3": ((scope.$index+1) == 3)}'>{{scope.$index + 1}}</p>
                        </template>
                    </el-table-column>
                    <el-table-column prop="username" label="Id"></el-table-column>
                    <el-table-column prop="profit" :label="$t('main.priceList.income')">
                        <template slot-scope="scope">
                            {{scope.row.profit / 100}}
                        </template>
                    </el-table-column>
                </el-table>
            </div>
            <div v-else-if="type === 'icon'">
                <el-table :data="dataList" height="267" :empty-text="$t('main.priceList.NoData')">
                    <el-table-column
                        prop="name"
                        :label="sceenWidth < 1300 ? $t('main.Currency.nameAndOdds') : $t('main.Currency.name')"
                        min-width="110"
                        align="left"
                    >
                        <template slot-scope="scope">
                            <i
                                class="el-icon-star-on pointer hoverColor"
                                :class="{active: scope.row.collectionStatus}"
                                @click="Favorites(scope.row)"
                            ></i>
                            <span :style="{'font-size': sceenWidth <= 1300 ? '10px' : ''}">
                                {{scope.row.code}}
                            </span>
                            <span v-if="sceenWidth <= 1300">
                                <span style="color:#1DD086">
                                <i class="el-icon-arrow-up"></i>
                                {{(chartData[scope.row.code].up)+'%'}}
                            </span>
                            <span style="margin:0 2px">/</span>
                            <span style="color:#E62250">
                                <i class="el-icon-arrow-down"></i>
                                {{(chartData[scope.row.code].down)+'%'}}
                            </span>
                            </span>
                        </template>
                    </el-table-column>
                    <!-- 屏幕1300加显示 -->
                    <el-table-column prop="bets" :label="$t('main.Currency.Odds')" min-width="150" v-if="sceenWidth > 1300">
                        <template slot-scope="scope">
                            <span style="color:#1DD086">
                                <i class="el-icon-arrow-up"></i>
                                {{(chartData[scope.row.code].up)+'%'}}
                            </span>
                            <span style="margin:0 2px">/</span>
                            <span style="color:#E62250">
                                <i class="el-icon-arrow-down"></i>
                                {{(chartData[scope.row.code].down)+'%'}}
                            </span>
                        </template>
                    </el-table-column>
                    <!-- 屏幕1810加显示 -->
                    <el-table-column prop="change" :label="$t('main.Currency.amplitude')" v-if="sceenWidth > 1810"></el-table-column>
                    <el-table-column
                        prop="Volume"
                        :label="$t('main.Currency.TradingVolume')"
                        min-width="120"
                         v-if="sceenWidth > 2200"
                    ></el-table-column>
                </el-table>
            </div>
        </div>
    </div>
</template>

<script>
import {mapState} from 'vuex'
export default {
    props: {
        // 数据源
        dataList: {
            type: Array,
            default: []
        },
        // 类型 有三个地方用到这个组件 icon币种模块  rank排行模块 price价格模块
        type: {
            type: String,
            default: ""
        }
    },
    name: "pricePublic",
    data() {
        return {};
    },
    computed: {
        ...mapState({
            sceenWidth: state => state.sceenWidth,
            chartData: state => state
        })
    },
    mounted (){
    },
    methods: {
        // 收藏
        Favorites(row) {
            let obj = {
                market: row.market,
                code: row.code,
                status: row.collectionStatus
            };
            let message = this.$t('main.tip.Collectionsuccess');
            if (row.collectionStatus == 1) {
                message = this.$t('main.tip.Cancelsuccess');
            }
            this.$http.Collection(obj).then(res => {
                if (res) {
                    this.$message(message);
                    this.$emit("collectionCallback");
                }
            });
        },
        tablePriceCellClassName({ row, column, rowIndex, columnIndex }) {
            if (columnIndex == 2) {
                return "price-time";
            }
            if (columnIndex < 2) {
                return rowIndex % 2 > .5 ? 'price-increase' : 'price-decrease';
                // return row.direction == 'increase' ? 'price-increase' : 'price-decrease'
            }
        },
        rankingCellClassName({ row, column, rowIndex, columnIndex }) {
            if (columnIndex == 0 && row) {
                return "ranking ranking-" + row.Ranking;
            }
        }
    },
    components: {}
};
</script>

<style scoped lang="scss">
/deep/ .el-table {
    color: #fff;
    font-size: 12px;
    background: #202432;
    .el-table__header-wrapper{
        height: 25px;
        .has-gutter tr{
            height: 25px;
        }
    }

    ::-webkit-scrollbar-track-piece {
        //滚动条凹槽的颜色，还可以设置边框属性
        background-color: #202432;
    }
    ::-webkit-scrollbar {
        //滚动条的宽度
        width: 6px;
    }
    ::-webkit-scrollbar-thumb {
        border-radius: 3px;
        background-color: #7d8da1;
        background-clip: padding-box;
    }
    ::-webkit-scrollbar-thumb:hover {
        background-color: #bbb;
    }

    &::before {
        background: #202432;
    }
    td,
    th,
    th.is-leaf {
        text-align: center;
        border: none;
    }
    th {
        background: #191c25;
        padding: 0;
        color: #fff;

        @media screen and (max-width: 900px) {
            background: transparent !important;
            border-top: 1px solid #787b89 !important;
            border-bottom: 1px solid #787b89 !important;
        }
    }
    tr {
        background: #202432;
        &:hover {
            td {
                background: #202432 !important;
            }
        }
    }

    td {
        padding: 0;
        &.is-left {
            text-align: left;
            .cell {
                padding-left: 15px;
                padding-right: 0;
            }
        }

        &.price-time {
            color: #787b89;
        }
        &.price-increase {
            color: #1dd086;
        }
        &.price-decrease {
            color: #e62250;
        }

        &.ranking {
            .cell {
                width: 30px;
                height: 30px;
                margin: auto;
                line-height: 30px;
            }
        }
        .ranking-1 {
            height: 30px;
            line-height: 40px;
            background: url("../assets/place-first-combined.png") no-repeat
                bottom;
            background-size: 30px 30px;
        }
        .ranking-2 {
            background: url("../assets/place-second.png") no-repeat center;
            background-size: 30px 30px;
        }
        .ranking-3 {
            background: url("../assets/place-third.png") no-repeat center;
            background-size: 30px 30px;
        }
    }

    .el-table__empty-block {
        width: auto !important;
    }

    table {
        width: auto !important;
        td,
        th {
            @media screen and (max-width: 900px) {
                font-size: 9px;
            }
            .cell {
                padding: 0 2px !important;
            }
        }
    }
}

.hoverColor {
    font-size: 20px;
    line-height: 24px;
    &.active {
        color: #ffc000;
    }
    &:hover {
        color: #ffc000;
    }
}

@media screen and (max-width: 900px) {
    .amount-input-container {
        padding-bottom: 5px !important;
    }
}
</style>
