 * @Descripttion: 首页币种表格
 * @Author: wangxi
 * @Date: 2019-11-30 18:57:24
 * @LastEditors: wangxi
 * @LastEditTime: 2019-12-02 11:57:08
 -->

<template>
    <div class="GeneralPurpose">
        <el-table class="mobile-hide" :data="tableData" style="width: 100%" height="340px" :empty-text="$t('main.priceList.NoData')">
            <el-table-column align="center" width="50px">
                <template slot-scope="scope">
                    <i v-if="loginStatus" class="el-icon-star-on pointer hoverColor" :class='{"isCollection" : (scope.row.collec == 1)}' @click="Favorites(scope.row)"></i>
                </template>
            </el-table-column>
            <el-table-column
                prop="code"
                :label="$t('main.HomeList.Name')"
                align="center"
                width="100px"
            >
                <template slot-scope="scope">
                    <span style="cursor: pointer;" @click="jumoTransactionPage">{{scope.row.code}}</span>
                </template>
            </el-table-column>
            <el-table-column :label="$t('main.HomeList.UpTrades')" align="center" width="200px">
                <template slot-scope="scope">
                    <span style="color:#1DD086">
                        <i class="el-icon-arrow-up"></i>
                        {{(chartData[scope.row.code].up)+'%'}}
                    </span>
                    <span style="margin:0 5px">/</span>
                    <span style="color:#E62250">
                        <i class="el-icon-arrow-down"></i>
                        {{(chartData[scope.row.code].down)+'%'}}
                    </span>
                </template>
            </el-table-column>
            <el-table-column prop="Price" :label="$t('main.HomeList.Price')" align="center"></el-table-column>
            <el-table-column prop="Change" :label="$t('main.HomeList.Change')" align="center">
                <template slot-scope="scope">
                    <span
                        v-if="scope.row.Change >= 0"
                        style="color:#1DD086;"
                    >{{'+'+scope.row.Change + '%'}}</span>
                    <span v-else style="color:#E62250;">{{scope.row.Change + '%'}}</span>
                </template>
            </el-table-column>
            <el-table-column prop="High" :label="$t('main.HomeList.High')" align="center">
                <template slot-scope="scope">
                    <span style="color:#1DD086;">{{scope.row.High}}</span>
                </template>
            </el-table-column>
            <el-table-column prop="Low" :label="$t('main.HomeList.Low')" align="center">
                <template slot-scope="scope">
                    <span style="color:#E62250;">{{scope.row.Low}}</span>
                </template>
            </el-table-column>
            <el-table-column
                prop="Volume"
                :label="$t('main.HomeList.Volume')"
                align="center"
                width="150px"
            ></el-table-column>
            <el-table-column
                prop="Volume"
                :label="$t('main.HomeList.Access')"
                align="center"
                width="150px"
            >
                <template slot-scope="scope">
                    <i class="el-icon-s-promotion pointer" style="color:#fff" @click="jumoTransactionPage"></i>
                </template>
            </el-table-column>
        </el-table>
        <table class="mobile-show mobile-table">
            <thead>
                <tr>
                    <th>{{$t('main.HomeList.Name')}}</th>
                    <th>{{$t('main.HomeList.UpTrades')}}</th>
                    <th>{{$t('main.HomeList.Price')}}</th>
                </tr>
            </thead>
            <tbody>
                <!-- Sample data -->
                <tr>
                    <td>
                        <div class="first-row name">Bitcoin Index</div>
                        24h Change <span class="up">+23.13%</span>
                    </td>
                    <td>
                        <div class="first-row up-down">
                            <span class="up">
                                <i class="el-icon-arrow-up"></i>167%
                                </span>/<span class="down"><i class="el-icon-arrow-down"></i>189%</span>
                        </div>
                        Vol 10,214
                    </td>
                    <td>
                        <div class="first-row price">9112.0412</div>
                        <button class="trade-btn">Trade</button>
                    </td>
                </tr>
                <!-- Sample data end -->
                <tr v-for="(item, i) in tableData" :key="i">
                    <td>
                        <div class="first-row name">(data name)</div>
                        24h Change <span class="up">(change value)</span>
                    </td>
                    <td>
                        <div class="first-row up-down">
                            <span class="up">
                                <i class="el-icon-arrow-up"></i>(167%)</span> /
                                <span class="down"><i class="el-icon-arrow-down"></i>189%</span>
                        </div>
                    </td>
                    <td>
                        <div class="first-row price">(data price)</div>
                        <button class="trade-btn">Trade</button>

                    </td>
                </tr>
            </tbody>
        </table>
    </div>
</template>

<script>
import { mapState } from "vuex";
export default {
    name: "GeneralPurpose",
    props: ["tableData"],
    data() {
        return {
            loginStatus: false
        };
    },
    watch:{
        SuspensionEndTime:function(){
            this.getOddsTion()
        }
    },
    created() {
        this.loginStatus = this.$common.getCookie('token')
        this.getOddsTion()
    },
    methods: {
        getOddsTion(){
            this.$http.ProductList().then((res) => {
                let dataMap = res.data.data;
                Object.keys(dataMap).forEach((key) => {
                    this.$store.state[key] = dataMap[key]
                });
            })
        },
        jumoTransactionPage (){
            this.$router.push({
                name: 'transactionPage'
            })
        },
        Favorites(msg) {
            let index = 0;
            if(msg.market == '1'){
                index = 2;
            }else if(msg.market == 'fx'){
                index = 1;
            }else if(msg.market == '2'){
                index = 3;
            }
            let obj = {
                market: msg.market,
                code: msg.code,
                status: msg.collec
            };
            let message = '收藏成功';
            if(msg.collec == 1){
                message = '取消成功';
            }
            this.$http.Collection(obj).then(res => {
                if(res){
                    this.$notify({
                        title: '成功',
                        message: message,
                        type: 'success'
                    });
                    this.$emit("getData",index);
                }
            });
            
        }
    },
    computed: {
        ...mapState({
            chartData: state => state,
            SuspensionEndTime: state => state.SuspensionEndTime,
        }),
    }
};
</script>

<style lang="scss">
.GeneralPurpose {
    .el-table tbody tr:hover > td {
        background-color: #202432 !important;
    }
    .el-table {
        background: #202432;
        font-size: 12px;
    }
    .isCollection {
        color: #ffc000;
    }
    .el-table__body,
    .el-table__footer,
    .el-table__header {
        .has-gutter tr th {
            background: #191c25;
            color: #dddee5;
            padding: 2px 0;
            border-bottom: none;
        }
    }
    .el-table::before {
        height: 0;
    }
    .el-table__body-wrapper tr td {
        background: #202432;
        border-bottom: none;
        color: #dddee5;
    }
    .hoverColor {
        font-size: 20px;
        line-height: 24px;
        &:hover {
            color: #ffc000;
        }
    }

    @media screen and (max-width: 768px) {
        .mobile-table {
            text-align: left;
            display: table;
            width: calc(100% - 20px);
            margin: auto;
            font-size: 12px;
            color: #787b89;
            thead {
                tr {
                    border-bottom: 1px solid #787b89;
                    th {
                        padding: 16px 0 12px 0px;
                    }
                }
                
            }
            tbody {
                tr {
                    border-bottom: 1px solid #787b89;
                    td {
                        padding: 10px 0 5px 0px;

                        .first-row {
                            height: 25px;

                            &.name {     
                                font-size: 16px;
                                color: #dddee5;
                            }

                            &.price {
                                color: #dddee5;
                            }
                        }

                        .trade-btn {
                            background-color: #3f434e;
                            cursor: pointer;
                            color: #dddee5;
                            border: none;
                            padding: 1px 12px;
                            border-radius: 2px;
                            margin-top: -7px;
                        }
                    }
                }

            }
        }
    }
}
</style>
