<template>
    <div id="chart-box" class="chart-box">
        <div :class="{'attackEffect' : (currentIndex >= 40 && currentIndex <= 49)}"></div>
        <div class="chart-wrapper">
            <v-chart
                style="width: 100%;height: 100%"
                ref="chart"
                :autoresize="true"
                id="chart"
                :options="option"
            />
        </div>
        <!-- 当前圆点 -->
        <div
            v-if="chartLoadingStatus"
            :style="{top:0,left:startY + 'px',transform:' translateY('+ startx +'px)'}"
            class="position-a circle"
        >
            <div class="pulse"></div>
            <div class="pulse1"></div>
        </div>
        <!-- 标志价格横线 -->
        <div
            v-if="chartLoadingStatus && sceenWidth > 768"
            :style="{left:startY + 10 + 'px', borderColor: '#494869',borderWidth: '1px',position: 'absolute',top:startx + 4 +'px',borderStyle: 'dashed',height: 0, width: lastpointValue - startY + 'px'}"
        ></div>
        <!-- 当前价格 -->
        <div
            :style="{top: startx ? startx - 7 +'px' : '-300px', right: 30 + 'px', display: sceenWidth > 768 ? 'block' : 'none'}"
            class="position-a endBorder"
        >
            <div>{{closePrice}}</div>
        </div>
    </div>
</template>

<script>
import { mapState } from "vuex";
import ECharts from "vue-echarts";
import "echarts/lib/chart/line";
import "echarts/lib/component/markArea";
import "echarts/lib/component/markLine";
import "echarts/lib/component/markPoint";
import "echarts/lib/component/tooltip"; //引入图例组件
export default {
    name: "BussinessChart",
    components: {
        "v-chart": ECharts
    },
    data() {
        return {
            option: {
                animation: true,
                animationDuration: 1500,
                tooltip: {
                    trigger: "axis",
                    formatter: function(params) {
                        return "";
                    }
                },
                xAxis: {
                    type: "time",
                    boundaryGap: false,
                    splitLine: {
                        show: false
                    },
                    minInterval: 1,
                    axisLine: {
                        lineStyle: {
                            color: "#333",
                            width: 1
                        }
                    },
                    axisLabel: {
                        textStyle: {
                            color: "#a0a0c1"
                        }
                    },
                    splitNumber: 20
                },
                yAxis: {
                    type: "value",
                    splitLine: {
                        show: false
                    },
                    axisLine: {
                        lineStyle: {
                            color: "#333",
                            width: 1
                        }
                    },
                    axisLabel: {
                        formatter: function(value, index) {
                            return value.toFixed(2);
                        },
                        textStyle: {
                            color: "#a0a0c1"
                        }
                    },
                    min: function(value) {
                        return Math.floor(value.min - 1);
                    },
                    max: function(value) {
                        return Math.floor(value.max + 1);
                    },
                },
                series: [
                    {
                        data: [],
                        type: "line",
                        symbol: "none",
                        //区域填充样式
                        lineStyle: {
                            normal: {
                                color: "#fff" // 线条颜色
                            }
                        },
                        // connectNulls: true,
                        lineStyle: {
                            color: "#DDDDDD"
                        },
                        markLine: {
                            silent: true,
                            symbol: "none",
                            lineStyle: {
                                type: "solid",
                                width: 1
                            },
                            itemStyle: [
                                {
                                    color: {
                                        type: "linear",
                                        x: 0,
                                        y: 0,
                                        x2: 0,
                                        y2: 1,
                                        // colorStops: [
                                        //   {
                                        //     offset: 0,
                                        //     color: "#1b1b2e" // 0% 处的颜色
                                        //   },
                                        //   {
                                        //     offset: 1,
                                        //     color: "red" // 100% 处的颜色
                                        //   }
                                        // ],
                                        global: false // 缺省为 false
                                    }
                                }
                            ],
                            data: [
                                {
                                    lineStyle: {
                                        color: "red"
                                    },
                                    xAxis: 1573467302915
                                },
                                {
                                    lineStyle: {
                                        color: "pink"
                                    },
                                    xAxis: 1573467305414
                                }
                            ]
                        },
                        areaStyle: {
                            normal: {
                                color: {
                                    type: "linear",
                                    x: 0,
                                    y: 0,
                                    x2: 0,
                                    y2: 1,
                                    colorStops: [
                                        {
                                            offset: 0,
                                            color: "#DDDDDD" // 0% 处的颜色
                                        },
                                        {
                                            offset: 1,
                                            color: "rgba(0,0,0,0)" // 100% 处的颜色
                                        }
                                    ],
                                    global: false // 缺省为 false
                                },
                                shadowColor: "rgba(0, 0, 0, 0.1)",
                                shadowBlur: 10
                            }
                        }
                    }
                ]
            },
            bar: null, //图表
            timeArr: [], //x轴数据
            showData: [], //y轴数据
            len: 60, //60秒，用来拼接x轴的数据
            currentIndex: new Date().getSeconds(), //当前所处的秒数
            nextTime: null, //马上要请求的秒数
            timeDiff: 24 * 60 * 60 * 1000, //与美国的时差戳
            startx: 0,
            startY: 0,
            activityY: 0,
            activityX: 20,
            lastNum: 0, //交易值
            activeNum: 0, //当前的数值
            buyIndex: 63, //交易的秒数,用于给交易时间画点
            isEndBuyStatus: false, //第二个小圆点和它的价格显示标志
            lastTenpointValue: 1000, //两根竖线的坐标
            lastpointValue: -999,
            chartLoadingStatus: false, //图表加载完再显示那些圆点和价格
            needEmptyStatus: false, //是否清空数据
            timer: null,
            websock: null,
            SuspensionEndTime: "",
            websockData: [
                {
                    closePrice: 0
                }
            ],
            isNeedInitStatus: true, //是否重新绘制图表（一分钟）
            splitNumber: 20,
            math: 1450 //随机数
        };
    },
    computed: {
        ...mapState({
            currentBuyInfo: state => state.currentBuyInfo,
            sceenWidth: state => state.sceenWidth,
        }),
        closePrice() {
            return this.websockData[0].closePrice || 0;
        }
    },
    created() {},
    destroyed() {
        this.websock.close(); //离开路由之后断开websocket连接
    },
    mounted() {
        this.initWebSocket();
        this.bar = this.$refs.chart.chart;
        // 根据屏幕宽度调整x轴分隔符数量
        const sceenWidth = document.body.clientWidth;
        this.resetSplitNumber(sceenWidth);
        window.addEventListener("resize", e => {
            this.resetSplitNumber(e.target.innerWidth);
        });
        // 图表测试数据
        // this.testData()
    },
    methods: {
        resetSplitNumber(width) {
            if (width <= 1440) {
                this.splitNumber = 10;
            }
            if (width <= 768) {
                this.splitNumber = 5;
            }
        },
        testData() {
            setInterval(() => {
                this.math =
                    this.math +
                    Math.random() * Math.random() * 0.5 -
                    Math.random() * Math.random() * 0.5;
                var obj = {
                    code: "XAUUSD",
                    market: "fx",
                    period: "new",
                    price: this.math,
                    signals: "dfcf_fx_XAUUSD_001",
                    time: this.$date.formatDateTime(new Date()),
                    volume: "2.931519/0.002015"
                };
                this.handleChartWebsocketData(JSON.stringify(obj));
            }, 1000);
        },
        initWebSocket() {
            //初始化weosocket
            const wsuri = "ws://192.168.101.13:4000/websocket/1321231";
            this.websock = new WebSocket(wsuri);
            this.websock.onmessage = this.websocketonmessage;
            this.websock.onopen = this.websocketonopen;
            this.websock.onerror = this.websocketonerror;
            this.websock.onclose = this.websocketclose;
        },
        websocketonopen() {
            //连接建立之后执行send方法发送数据
        },
        websocketonerror() {
            //连接建立失败重连
            this.initWebSocket();
        },
        websocketonmessage(e) {
            this.handleChartWebsocketData(e.data);
        },
        websocketsend(Data) {
            //数据发送
            this.websock.send(Data);
        },
        websocketclose(e) {
            //关闭
            console.log("断开连接", e);
        },
        handleChartWebsocketData(chartData) {
            //数据接收
            if (chartData === "连接成功") {
                return false;
            }
            const regx = /\'/g;
            // 将单引号转为双引号然后转为json
            const redata = JSON.parse(chartData);
            this.$store.commit("setChartData", redata);
            let serverTime = redata.time;
            this.websockData = [
                {
                    server_time: new Date(serverTime).getTime(),
                    closePrice: redata.price
                }
            ];
            if (
                this.websockData[0].server_time - this.initTimeStamp >
                59 * 1000
            ) {
                this.$emit("endTrade", this.initTimeStamp);
                this.isNeedInitStatus = true;
            }

            this.currentIndex = this.formatDate(
                2,
                new Date(serverTime).getTime()
            );
            if (this.isNeedInitStatus) {
                // this.currentIndex = this.formatDate(2, new Date(serverTime).getTime());
                this.initTimeStamp =
                    this.websockData[0].server_time - this.currentIndex * 1000;
                this.chartLoadingStatus = false;
                this.init(this.websockData);
                this.chartLoadingStatus = true;
                this.isNeedInitStatus = false;
            }

            // 获取当前价格标签位置，用来定义当前价格横线的宽度
            this.lastpointValue = document.getElementsByClassName(
                "endBorder"
            )[0].offsetLeft;

            this.refreshData(this.websockData);
        },
        // 获取时间秒数
        getDateSecond(dateString) {
            return dateString.split(" ")[1].split(":")[2];
        },
        init() {
            this.showData = [];
            // 首先先创一个有61个时间戳的数组
            this.showData = new Array(this.len + 1).fill(0);
            this.showData.forEach((v, i) => {
                this.showData[i] = [this.initTimeStamp + i * 1000, ""];
            });
            this.SuspensionEndTime = this.showData[60][0];
            this.$store.dispatch(
                "setSuspensionEndTime",
                this.SuspensionEndTime
            );
            this.draw();
        },
        formatDate(type, timeStamp) {
            let now = new Date(timeStamp);
            var hour = now.getHours(); //返回日期中的小时数（0到23）
            var minute = now.getMinutes(); //返回日期中的分钟数（0到59）
            var seconds = now.getSeconds(); //返回日期中的分钟数（0到59）
            if (type === 0) {
                return hour;
            } else if (type === 1) {
                return minute;
            } else if (type === 2) {
                return seconds;
            }
        },
        addZero(num) {
            return num < 10 ? "0" + num : num;
        },
        resolutionFormat(resolution) {
            if (resolution.includes("D")) return parseInt(resolution) + "D";
            if (resolution.includes("W")) return parseInt(resolution) + "W";
            if (resolution / 60 < 1) return resolution + "M";
            if (resolution / 60 >= 1 && resolution / (24 * 60) < 1)
                return resolution / 60 + "H";
            return "";
        },
        refreshData(websockData) {
            websockData.forEach(v => {
                this.showData.push([v.server_time, v.closePrice]);
            });

            this.draw();
        },
        randomData() {
            let num = Math.random() + 1490;
            return num.toFixed(2);
        },
        draw() {
            let obj = JSON.parse(JSON.stringify(this.option));
            // 最大值最小值
            obj.yAxis.min = function(value) {
                return value.min - 0.02;
            };
            obj.yAxis.max = function(value) {
                return value.max + 0.02;
            };
            obj.yAxis.axisLabel = {
                formatter: function(value, index) {
                    return value.toFixed(2);
                },
                textStyle: {
                    color: "#a0a0c1"
                }
            }
            // x轴分隔符
            obj.xAxis.splitNumber = this.splitNumber;

            // 截止线
            let arr = [
                {
                    lineStyle: {
                        color: {
                            type: "linear",
                            x: 0,
                            y: 0,
                            x2: 0,
                            y2: 1,
                            colorStops: [
                                {
                                    offset: 0,
                                    color: "#1b1b2e" // 0% 处的颜色
                                },
                                {
                                    offset: 1,
                                    color: "#ca2626" // 100% 处的颜色
                                }
                            ]
                        }
                    },
                    label: {
                        show: true,
                        normal: {
                            show: true,
                            textStyle: {
                                fontSize: 14,
                                color: "#ca2626"
                            },
                            formatter: function(params) {
                                return params.value.split(" ")[1];
                            }
                        }
                    },
                    xAxis: this.showData[this.len - 10]
                        ? this.$date.formatDateTime(
                              this.showData[this.len - 10][0]
                          )
                        : ""
                },
                {
                    lineStyle: {
                        color: {
                            type: "linear",
                            x: 0,
                            y: 0,
                            x2: 0,
                            y2: 1,
                            colorStops: [
                                {
                                    offset: 0,
                                    color: "#1b1b2e" // 0% 处的颜色
                                },
                                {
                                    offset: 1,
                                    color: "#439957" // 100% 处的颜色
                                }
                            ]
                        }
                    },
                    label: {
                        show: true,
                        normal: {
                            show: true,
                            textStyle: {
                                fontSize: 14,
                                color: "#439957"
                            },
                            formatter: function(params) {
                                return params.value.split(" ")[1];
                            }
                        }
                    },
                    xAxis: this.showData[this.len]
                        ? this.$date.formatDateTime(this.showData[this.len][0])
                        : ""
                }
            ];

            // 鼠标放上去的提示
            obj.tooltip = {
                trigger: "axis",
                formatter: function(params) {
                    // 因为最开始弄进去了很多假数据，在这里处理鼠标移上去不显示空数据
                    let data = "";
                    params.forEach(v => {
                        if (v.data[1]) {
                            data = v.data[1];
                        }
                    });
                    const time = params[0].axisValueLabel;

                    if (data) {
                        return `<p>${time}<br>price：${data}</p>`;
                    } else {
                        return "";
                    }
                }
            };

            // 当前购买的订单标记
            this.$common.setCookie(
                "currentBuyInfo",
                this.currentBuyInfo.join(",")
            );
            obj.series[0].markPoint = {
                symbolSize: "40",
                label: {
                    normal: {
                        formatter: function(param) {
                            return param != null ? Math.round(param.value) : "";
                        }
                    }
                },
                data: this.currentBuyInfo,
                tooltip: {
                    formatter: function(param) {
                        return param.name + "<br>" + (param.data.coord || "");
                    }
                }
            };

            obj.series[0].animationEasing = function(k) {
                return k;
            };

            obj.series[0].animationDuration = 2000;
            obj.series[0].markLine.data = arr;
            obj.series[0].data = this.showData;

            this.option = obj;
            this.onClick();
        },
        onClick() {
            setTimeout(() => {
                let obj = JSON.parse(JSON.stringify(this.option)).series[0]
                    .data;
                let arr = this.bar.getViewOfSeriesModel(
                    this.bar.getModel().getSeries()[0]
                )._points;
                if (arr.length >= 62) {
                    let lastOne = arr[arr.length - 1];
                    this.startY = lastOne[0] - 5;
                    this.startx = lastOne[1] - 5;
                    this.activeNum = this.showData[
                        this.currentIndex + this.len
                    ];
                }
            }, 30);
        },
        getXpointValue(i) {
            let arr = this.bar.getViewOfSeriesModel(
                this.bar.getModel().getSeries()[0]
            )._points;
            let step;
            if (arr.length > this.len + 2) {
                arr.forEach((v, index) => {
                    if (index > this.len + 1) {
                        step = arr[index][0] - arr[index - 1][0];
                    }
                });
                return step * i + arr[this.len + 1][0];
            }

            return 0;
        },
        buyTime() {
            let arr = this.bar.getViewOfSeriesModel(
                this.bar.getModel().getSeries()[0]
            )._points;
            if (arr.length > 61 && this.buyIndex <= arr.length) {
                let lastOne2 = arr[this.buyIndex - 1];
                this.activityY = lastOne2[0] - 5;
                this.activityX = lastOne2[1] - 5;
                this.lastNum = this.showData[this.buyIndex - 1];
                this.isEndBuyStatus = true;
            }
        }
    }
};
</script>

<style lang='scss' scoped>
.attackEffect {
    //被攻击特效
    position: absolute;
    width: 100%;
    height: 100%;
    border: 1px solid rgba(0, 244, 255, 1);
    box-sizing: border-box;
    box-shadow: inset 0px 0px 40px rgba(0, 244, 255, 1);
    animation: mymove 2s infinite;
    @keyframes mymove {
        0% {
            opacity: 0;
        }
        50% {
            opacity: 1;
        }
        100% {
            opacity: 0;
        }
    }
}
.chart-box {
    width: 100%;
    height: 540px;
    position: relative;
    // background: url("../assets/bj.png") no-repeat 100% 100%;
    background: #1b1b2e;
    .chart-wrapper {
        width: 100%;
        height: 100%;
        position: relative;
    }
}
.circle {
    width: 10px;
    height: 10px;
    border-radius: 50%;
    background: #dddddd;
    // transition: all 0.3s;
}
.position-a {
    position: absolute;
}
.startBorder,
.endBorder {
    height: 24px;
    z-index: 11;
    text-align: center;
    div {
        width: 100%;
        height: 24px;
        background: #ff9100;
        z-index: 3;
        padding: 0 10px 0 5px;
        color: #fff;
        font-size: 12px;
        line-height: 24px;
        white-space: nowrap;
    }
}
.startBorder div::after {
    content: "";
    position: absolute;
    right: -12px;
    top: 0;
    width: 0;
    height: 0;
    border-width: 12px 0 12px 12px;
    border-style: solid;
    border-color: transparent transparent transparent #ff9100;
}
.endBorder div {
    background: #494869;
}
.endBorder div::before {
    content: "";
    position: absolute;
    left: -12px;
    top: 0;
    width: 0;
    height: 0;
    border-width: 12px 12px 12px 0;
    border-style: solid;
    border-color: transparent #494869 transparent transparent;
}
.line {
    background: linear-gradient(160deg, transparent 5%, #ca2626 90%);
    height: 90%;
    width: 1px;
    p {
        line-height: 18px;
        width: 60px;
        position: relative;
        left: -30px;
        top: 0px;
        color: #ca2626;
        font-size: 14px;
    }
}
.line2 {
    background: linear-gradient(160deg, transparent 5%, #439957 90%);
    p {
        color: #439957;
    }
}

/* 保持大小不变的小圆圈  */
.dot {
    position: absolute;
    left: 15px;
    top: 15px;
    width: 10px;
    height: 10px;
    border-radius: 50%;
    background: #dddddd;
    z-index: 2;
}

@keyframes warn {
    0% {
        transform: scale(0);
        -webkit-transform: scale(0);
        opacity: 0.1;
    }
    100% {
        transform: scale(0.25);
        -webkit-transform: scale(0.25);
        opacity: 0.5;
    }
}
@keyframes warn1 {
    0% {
        transform: scale(0);
        -webkit-transform: scale(0);
        opacity: 0.1;
    }
    100% {
        transform: scale(0.4);
        -webkit-transform: scale(0.4);
        opacity: 0.5;
    }
}

/* 产生动画（向外扩散变大）的圆圈  */
.pulse {
    position: absolute;
    width: 50px;
    height: 50px;
    left: -23.5px;
    top: -23.5px;
    border: 3px solid #dddddd;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    border-radius: 50%;
    z-index: -1;
    opacity: 0;
    -webkit-animation: warn 1s ease-out;
    -moz-animation: warn 1s ease-out;
    animation: warn 1s ease-out;
    -webkit-animation-iteration-count: infinite;
    -moz-animation-iteration-count: infinite;
    animation-iteration-count: infinite;
}
.pulse1 {
    position: absolute;
    width: 50px;
    height: 50px;
    left: -23.5px;
    top: -23.5px;
    border: 3px solid #dddddd;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    border-radius: 50%;
    z-index: -1;
    opacity: 0;
    -webkit-animation: warn1 1s ease-out;
    -moz-animation: warn1 1s ease-out;
    animation: warn1 1s ease-out;
    -webkit-animation-iteration-count: infinite;
    -moz-animation-iteration-count: infinite;
    animation-iteration-count: infinite;
}
</style>