<template>
<!-- 持仺/历史记录 -->
    <div id="Suspension" class="Suspension">
        <el-table :data="OrderData" style="width: 100%" :height="domHeight" :empty-text="$t('main.priceList.NoData')">
            <el-table-column
                prop="marketCode"
                :label="$t('main.OrderRecord.Name')"
                align="center"
                width="100px"
            ></el-table-column>
            <el-table-column prop="tradeType" :label="$t('main.OrderRecord.Time')" align="center">
                <template slot-scope="scope">{{(scope.row.tradeType).split('_')[1]}}</template>
            </el-table-column>
            <el-table-column
                prop="Access"
                :label="$t('main.OrderRecord.Access')"
                align="center"
                width="150px"
            >
                <template slot-scope="scope">
                    <i class="el-icon-s-promotion pointer" style="color:#fff"></i>
                </template>
            </el-table-column>
            <el-table-column prop="tradeId" :label="$t('main.OrderRecord.OrderID')" align="center">
            </el-table-column>
            <el-table-column
                prop="Position"
                :label="$t('main.OrderRecord.Position')"
                align="center"
            >
                <template slot-scope="scope">
                    <span v-if="(scope.row.tradeType).split('_')[3] == 'B'" style="color:#1DD086">{{$t('main.OrderRecord.up')}}</span>
                    <span v-else style="color:#E62250">{{$t('main.OrderRecord.down')}}</span>
                </template>
            </el-table-column>
            <el-table-column
                prop="createTime"
                :label="$t('main.OrderRecord.OriginTime')"
                align="center"
            ></el-table-column>
            <el-table-column
                prop="marketPrice"
                :label="$t('main.OrderRecord.OriginPrice')"
                align="center"
            ></el-table-column>
            <el-table-column
                prop="tradePriceYuan"
                :label="$t('main.OrderRecord.Amount')"
                align="center"
            ></el-table-column>
            <el-table-column :label="$t('main.OrderRecord.CurrentPrice')" align="center" v-if="type == 'OpenPosition'">
                <template slot-scope="scope">
                    <span :style="{color: (chartData.price >= scope.row.marketPrice) ? '#1DD086' : '#e62250'}">
                        {{chartData.price}}
                    </span>
                </template>
            </el-table-column>
            <el-table-column :label="$t('main.OrderRecord.endPrice')" align="center" v-if="type == 'history'">
                <template slot-scope="scope">
                    <span>
                        {{scope.row.remark}}
                    </span>
                </template>
            </el-table-column>
            <el-table-column prop="settleRatio" :label="$t('main.OrderRecord.Odds')" align="center">
                <template slot-scope="scope">
                    <span>{{scope.row.settleRatio+'%'}}</span>
                </template>
            </el-table-column>
            <el-table-column prop="Payouts" :label="$t('main.OrderRecord.Payouts')" align="center" v-if="type == 'history'" >
                <template slot-scope="scope">
                    <span>{{scope.row.settlePriceYuan}}</span>
                </template>
            </el-table-column>
            <el-table-column :label="$t('main.OrderRecord.RemainingTime')" align="center" v-if="type == 'OpenPosition'">
                <template slot-scope="scope">
                    <span>{{Countdown}}</span>
                </template>
            </el-table-column>
        </el-table>
    </div>
</template>

<script>
import { mapState } from "vuex";
export default {
    name: "Suspension",
    props: ['OrderData','type'],
    data() {
        return {
            dataList: [],
            Countdown: "",
            domHeight: '0px'
        };
    },
    computed: {
        SuspensionEndTime() {
            return this.$store.state.SuspensionEndTime;
        },
        ...mapState({
            chartData: state => state.chartData,
            sceenWidth: state => state.sceenWidth
        })

    },
    created() {
        this.stateTime = this.$store.state.StartTime;
        this.endTime = this.$store.state.endTime;
    },
    mounted() {
        this.timer = setInterval(() => {
            this.RemainingTime();
            if(this.OrderData == [] || this.type == 'history'){
                clearInterval(this.timer)
                return
            }
        }, 1000);
        this.$nextTick(() => {
            // 直接设置不生效，放到this.$nextTick里才会生效
            const domOffset = document.getElementById('Suspension').getBoundingClientRect()
            const sceenHeight = document.body.clientHeight
            this.domHeight = sceenHeight - domOffset.top - 10 + 'px'
        })
    },
    beforeDestroy() {
        clearInterval(this.timer);
    },
    methods: {
        RemainingTime() {
            let SuspensionEndTime = this.SuspensionEndTime; //终点时间
            let date = new Date(SuspensionEndTime - Date.parse(new Date() ));
            // let m = date.getMinutes(); //获取分钟数(0-59)
            let s = date.getSeconds() ; //获取秒数(0-59)
            // if (m < 10) {
            //     m = "0" + m;
            // }
            if (s < 10) {
                s = "0" + s;
            }
            this.Countdown = "00：" + '00' + "：" + s;
        }
    }
};
</script>

<style lang="scss">
.Suspension {
    position: relative;
    /deep/ .el-table {
        table {
            width: 100% !important;
        }
        background: #202432;
        tr {
            background: #202432;
            height: 30px;
            td {
                padding: 3px;
            }
        }
        .cell {
            padding: 0;
            font-size: 11px;
            white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis;
        }

        ::-webkit-scrollbar-track-piece {
            //滚动条凹槽的颜色，还可以设置边框属性
            background-color: #202432;
        }
        ::-webkit-scrollbar {
            //滚动条的宽度
            width: 6px;
        }
        ::-webkit-scrollbar-thumb {
            border-radius: 3px;
            background-color: #7d8da1;
            background-clip: padding-box;
        }
        ::-webkit-scrollbar-thumb:hover {
            background-color: #bbb;
        }
        .el-table__empty-block {
            background: #202432;
        }
    }
    .el-table--enable-row-hover .el-table__body tr:hover > td {
        background: #202432;
    }
    .el-table__body,
    .el-table__footer,
    .el-table__header {
        .has-gutter tr th {
            background: #202432;
            color: #dddee5;
            padding: 8px 0;
            border-bottom: 1px solid rgba(120, 123, 137, 1);
        }
    }
    .el-table::before {
        height: 0;
    }
    .el-table__body-wrapper tr td {
        background: #202432;
        border-bottom: none;
        color: #dddee5;
    }
    .hoverColor {
        font-size: 20px;
        line-height: 24px;
        &:hover {
            color: #ffc000;
        }
    }
}
</style>
