<template>
    <div id="header">
        <div class="header mobile_header_box">
            <div class="return">
                <img src="../assets/back-icon.svg" @click="routerPath('/')" alt />
            </div>
            <div class="flex-spacer"></div>
            <div>{{chartData.code}}</div>
            <div class="flex-spacer"></div>
            <div class="star">
                <!-- <img src="../../assets/favorite.svg" alt=""> -->
                <img src="../assets/not-favorite.svg" alt />
            </div>
        </div>
        <div class="mobile-show mobile-header-placeholder"></div>
        <div class="header header_box flex_xc">
            <div class="header-left">
                <div class="header-logo">
                    <img src="../assets/okbix-logo-small.png" alt="OKBix" @click="routerPath('/')" />
                </div>
                <div class="btn_box flex_c_c">
                    <el-button size="medium" @click="routerPath('/transactionPage')">
                        <span v-text="$t('main.nav.transaction')"></span>
                    </el-button>
                    <el-button size="medium">{{$t('main.nav.Tutorial')}}</el-button>
                    <el-button
                        size="medium"
                        @click="routerPath('/assets')"
                    >{{$t('main.nav.Finance')}}</el-button>
                    <el-button
                        size="medium"
                        @click="routerPath('/assets/deposit')"
                    >{{$t('main.nav.Deposit')}}</el-button>
                </div>
            </div>
            <div class="header-right flex_c_c">
                <div class="r_btn flex_yc">
                    <div class="National_China" v-if="this.lang === 'zh'" @click="changeLangEvent"></div>
                    <div class="National_USA" v-else @click="changeLangEvent"></div>
                    <el-button
                        size="medium"
                        @click="changeLangEvent"
                    >{{$t('main.nav.LanguageaSwitching')}}</el-button>
                    <el-input
                        v-model.trim="searchText"
                        class="search-bar"
                        size="medium"
                        :placeholder="$t('main.nav.SearchText')"
                        clearable
                        style="max-width: 200px; width: 70%; margin-right:20px;"
                        @input="iconSearch"
                    ></el-input>
                </div>
                <div v-if="pageToken" class="r_user flex_c_c">
                    <img class="user-pic" src="../assets/eayon.jpg" alt />
                    <div class="header-user-info">
                        <div class="user-name">{{userInfo.username}}</div>
                        <el-dropdown class="available-funds" @command="loginOut">
                            <span class="el-dropdown-link">
                                {{$t('main.AccountPage.AccountBalance')}}：{{userInfo.coinAnalog / 100 || 0}}
                                <i
                                    class="el-icon-arrow-down el-icon--right"
                                ></i>
                            </span>
                            <el-dropdown-menu slot="dropdown">
                                <el-dropdown-item
                                    command="loginOut"
                                >{{$t('main.AccountPage.LogOut')}}</el-dropdown-item>
                            </el-dropdown-menu>
                        </el-dropdown>
                    </div>
                </div>
                <div v-else class="r_user flex_c_c">
                    <img class="user-pic" src="../assets/unknown-profile.png" alt />
                    <router-link class="login" :to="{name: 'login'}">{{$t('main.nav.Login')}}</router-link>
                    <div class="separator">{{$t('main.nav.Or')}}</div>
                    <router-link class="signup" :to="{name: 'register'}">{{$t('main.nav.Signup')}}</router-link>
                </div>
            </div>
        </div>
        <div class="mobile-show">
            <div class="mobile-nav-buttons">
                <div class="icon">
                    <div class="icon-img home" @click="routerPath('/')"></div>
                    <div>{{$t('main.nav.Home')}}</div>
                </div>
                <div class="icon">
                    <div class="icon-img orders" @click="routerPath('/orders')"></div>
                    <div>{{$t('main.nav.Orders')}}</div>
                </div>
                <div class="icon">
                    <div class="icon-img trade" @click="routerPath('/transactionPage')"></div>
                    <div>{{$t('main.nav.Trade')}}</div>
                </div>
                <div class="icon">
                    <div class="icon-img assets" @click="routerPath('/assets')"></div>
                    <div>{{$t('main.nav.Assets')}}</div>
                </div>
                <div class="icon">
                    <div class="icon-img account" @click="routerPath('/account')"></div>
                    <div>{{$t('main.nav.Accounts')}}</div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import { mapState } from "vuex";
export default {
    name: "home",
    data() {
        return {
            searchText: "",
            searchFun: null,
            lang: "zh",
            timer: null,
            pageToken: null,
            selectedMenu: "",
            getNum:true
        };
    },
    computed: {
        ...mapState({
            // 币种列表
            cloneIconDetailList: state => state.cloneIconDetailList,
            userInfo: state => state.userInfo,
            chartData: state => state.chartData,
            SuspensionEndTime: state => state.SuspensionEndTime,
        })
    },
    created() {
        this.lang = this.$i18n.locale;
        // 优先拿缓存里的语言类型
        const lang = this.$common.getCookie('local-lang')
        if(lang){
            this.lang = lang
            this.$i18n.locale = lang
        }
        // 搜索防抖
        this.searchFun = this.$common.debounce(this.filterIconData, 500);
        // 登录状态
        this.pageToken = this.$common.getCookie('token')
        // 每5秒获取一次用户信息
        this.getUserDetail()
        this.timer = setInterval(() => {
            this.getUserDetail()
        }, 5000)
    },
    watch:{
        chartData :function () {
            if(this.getNum){
                this.getOddsTion()
            }
            this.getNum = false;
        },
        SuspensionEndTime:function(){
            this.getOddsTion()
        }

    },
    methods: {
        getOddsTion(){
            let oddsObj = this.chartData;
            let obj = {
                market : oddsObj.market,
                code:oddsObj.code
            }
            this.$http.ProductList(obj).then((res) => {
                let Index = this.$store.state.chartData.code;
                this.$store.state.fallOdds = res.data.data[Index].down;
                this.$store.state.riseOdds = res.data.data[Index].up;
                let dataMap = res.data.data;
                Object.keys(dataMap).forEach((key) => {
                    this.$store.state[key] = dataMap[key]
                });
            })
        },
        updateMenu(selectedItem) {
            this.selectedMenu =  selectedItem;
        },
        getUserDetail (){
            let token = this.$common.getCookie('token')
            this.pageToken = token
            // 用户未登录就不去调接口
            if(!token){
                clearInterval(this.timer)
                return false
            }
            this.$store.dispatch('getUserDetail')
        },
        changeLangEvent() {
            if (this.lang === "zh") {
                this.lang = "en";
            } else {
                this.lang = "zh";
            }
            this.$common.setCookie('local-lang', this.lang)
            this.$i18n.locale = this.lang;
        },
        routerPath(route) {
            this.$router.push(route);
        },
        iconSearch(e) {
            this.searchFun();
        },
        // 本地搜索
        filterIconData() {
            let returnFilterIconData = this.cloneIconDetailList.filter(v =>
                v.code.toUpperCase().includes(this.searchText.toUpperCase())
            );
            this.$store.dispatch("setIconDetailList", returnFilterIconData);
        },
        loginOut (command){
            if(command === 'loginOut'){
                this.$http.loginOut().then(() => {
                    this.$common.removeCookie('token')
                    this.$message(this.$t('main.tip.Logoutsuccessful'))
                })
            }
        }
    },
    beforeDestroy (){
        // 清除定时器
        clearInterval(this.timer)
    }
};
</script>

<style lang="scss">
.home {
    .header {
        width: 100%;
        background-color: #191c25;
        font-family: "Roboto", sans-serif;
        line-height: 45px;
        color: #dddee5;
    }

    .mobile-show {
        display: none;
    }

    .mobile_header_box {
        display: none;
    }
    .header_box {
        height: 55px;
        
        .el-button {
            background-color: transparent;
            border: none;
            color: #dddee5;
            font-size: 13px;
            margin: 0;
            &:active {
                background-color: #485371;
            }
        }

        .header-left {
            width: 50%;
            max-width: 550px;
            display: flex;
            justify-content: space-evenly;
            padding-right: 30px;
            .header-logo {
                margin-left: 12%;
                font-size: 50px;
                img {
                    max-height: 25px;
                    cursor: pointer;
                }
            }
            .btn_box {
                width: 42%;
            }
        }
        .header-right {
            width: 50%;
            max-width: 600px;
            padding-left: 20px;
            margin-right: 5%;

            .National_China,
            .National_USA {
                width: 40px;
                height: 20px;
            }
            .National_China {
                background: url("../assets/China.jpg") no-repeat;
                background-size: cover;
            }
            .National_USA {
                background: url("../assets/USA.jpg") no-repeat;
                background-size: cover;
            }
            .user-pic {
                max-height: 38px;
                border-radius: 50%;
                margin-right: 15px;
            }
            .r_user {
                white-space: nowrap;
                padding-right: 17px;
                a {
                    font-size: 13px;
                    &.login {
                        color: #dddee5;
                        text-decoration: none;                  
                    }
                    &.signup {
                        color: #04affe;
                        text-decoration: none; 
                    }
                }
                .separator {
                    color: #787b89;
                    font-size: 13px;
                    padding: 0 10px;
                }
            }
            .search-bar.el-input {
                margin-left: 10px;
                input {
                    border: none;
                    border-radius: 0;
                    border-bottom: 1px solid #485371;
                    background-color: transparent;
                    height: 27px;
                    padding: 0 5px;
                    font-size: 13px;

                    &::-webkit-input-placeholder {
                        /* Chrome/Opera/Safari */
                        color: #485371;
                    }
                    &::-moz-placeholder {
                        /* Firefox 19+ */
                        color: #485371;
                    }
                    &:-ms-input-placeholder {
                        /* IE 10+ */
                        color: #485371;
                    }
                    &:-moz-placeholder {
                        /* Firefox 18- */
                        color: #485371;
                    }
                }
            }
            .header-user-info {
                margin: 2px 0;
                line-height: 12px;
                .user-name {
                    margin: 3px 0;
                    padding-left: 8px;
                    font-size: 12px;
                }
            }
            
            .available-funds.el-dropdown {
                span {
                    color: #787b89;
                    border: 1px solid #787b89;
                    border-radius: 20px;
                    background-color: transparent;
                    font-size: 11px;
                    padding: 1px 17px;
                    cursor: pointer;
                }
            }
        }
    }

    @media screen and (max-width: 1100px) { 
        .header_box {
            .el-button {
                padding: 0 6px;
            }
        }
    }

    @media screen and (max-width: 768px) {
        .mobile-hide{
            display: none;
        }
        .mobile-show {
            display: block;
        }
        .mobile-nav-placeholder {
            height: 40px;
        }
        .mobile-nav-buttons {
            padding: 7px 0;
            background-color: #303542;
            display: flex;
            justify-content: space-evenly;
            position: fixed;
            bottom: 0;
            width: calc(100vw);
            z-index: 10;
            cursor: pointer;
            text-align: center;

            .icon {
                color: #787B89;
                text-align: center;

                .icon-img {
                    height: 25px;
                    width: 25px;
                    background-size: contain;
                    background-repeat: no-repeat;
                    margin-bottom: 3px;
                    display: inline-block;
                }

                .home {
                    background-image: url('../assets/icons/mobile-nav-home.png');
                }
                .orders {
                    background-image: url('../assets/icons/mobile-nav-orders.png');
                }
                .assets {
                    background-image: url('../assets/icons/mobile-nav-assets.png');
                }
                .trade {
                    background-image: url('../assets/icons/mobile-nav-trade.png');
                }
                .account {
                    background-image: url('../assets/icons/mobile-nav-user.png');
                }

                &.active, &:hover {
                    color: #04AFFE;

                    .home {
                        background-image: url('../assets/icons/mobile-nav-home-blue.png');   
                    }
                    .orders {
                        background-image: url('../assets/icons/mobile-nav-orders-blue.png');
                    }
                    .assets {
                        background-image: url('../assets/icons/mobile-nav-assets-blue.png');
                    }
                    .trade {
                        background-image: url('../assets/icons/mobile-nav-trade-blue.png');
                    }
                    .account {
                        background-image: url('../assets/icons/mobile-nav-user-blue.png');
                    }
                }
            }
        }
        
        .header_box {
            display: none;
        }
        .mobile-header-placeholder {
            width: 100px;
            height: 45px;
        }
        .mobile_header_box {
            z-index: 10;
            position: fixed;
            padding-right: 10px;
            width: calc(100vw);
            display: flex;
            height: 45px;
            font-size: 25px;
            line-height: 45px;
            cursor: default;

            .return {
                cursor: pointer;
                img {
                    margin: 10px;
                    height: 25px;
                }
            }
            .star {
                cursor: pointer;
                margin-right: 10px;
                img{
                    margin: 10px;
                    height: 25px;
                }    
            }
            .flex-spacer {
                flex-grow: 1;
            }
        }
    }
}
</style>
