<!--
 * @Descripttion: 交易界面
 * @Author: wangxi
 * @Date: 2019-11-30 18:57:24
 * @LastEditors: wangxi
 * @LastEditTime: 2019-12-02 14:10:13
 -->
<template>
    <div class="transactionPage">
        <div class="main-content">
            <div class="nameIndex">
                <span class="mobile-hide">{{chartData.code}}</span>
                <div class="current-price">
                    <div class="current-price-label mobile-hide">{{$t('main.nav.CurrentPrice')}}:</div>
                    <div class="current-price-amount">{{chartData.price}}</div>
                    <div class="hour-change mobile-show">
                        24{{$t('main.nav.HourChange')}}：
                        <span style="color: #1dd086;">+0.35%</span>
                    </div>
                </div>
                <div class="center_l_t">
                    <div class="tab_box">
                        <el-tabs v-model="activeName" type="card" @tab-click="handleClick">
                            <el-tab-pane :label="$t('main.nav.minute1')" name="first"></el-tab-pane>
                            <el-tab-pane :label="$t('main.nav.minute5')" name="second"></el-tab-pane>
                        </el-tabs>
                        <div class="tab_box_r">
                            <p class="mobile-hide">
                                {{$t('main.nav.Odds')}}：
                                <span style="color: #1dd086;">
                                    <span class="direction-symbol">&#x25B2;</span>
                                    {{$store.state.riseOdds}}%
                                </span>/
                                <span style="color: #E62250;">
                                    <span class="direction-symbol">&#x25BC;</span>
                                    {{$store.state.fallOdds}}%
                                </span>
                            </p>
                            <p class="mobile-hide">
                                {{$t('main.nav.CycleChange')}}：
                                <span style="color: #1dd086;">+0.35%</span>
                            </p>
                        </div>
                    </div>
                </div>
                <div class="mobile-show volume-up-down">
                    <table>
                        <tr>
                            <td>{{$t('main.nav.Volume')}}</td>
                            <td class="volume">{{chartData.price}}</td>
                        </tr>
                        <tr>
                            <td>{{$t('main.nav.Up')}}</td>
                            <td class="up">{{$store.state.riseOdds}}%</td>
                        </tr>
                        <tr>
                            <td>{{$t('main.nav.Down')}}</td>
                            <td class="down">{{$store.state.fallOdds}}%</td>
                        </tr>
                    </table>
                </div>
            </div>
            <div class="center_box">
                <div class="desktop-reverse">
                    <div class="center_l">
                        <div id="chart-container" class="chart-container">
                            <bussiness-chart @endTrade="displayTradeResult"></bussiness-chart>
                            <div class="results-description">{{$t('main.chart.ResultHistory')}}</div>
                            <div class="results-history" id="results-history">
                                <div
                                    style="display: flex;transition: all 1s;"
                                    :style="{transform: `translateX(-${historyOffsetLeft}px)`}"
                                >
                                    <div class="fade-in mobile-show"></div>
                                    <div
                                        class="results-history-column"
                                        v-for="(item, index) in outcomeHistoryShowData"
                                        :key="index"
                                    >
                                        <img
                                            v-if="Number(item.status) === 0"
                                            src="../assets/success.svg"
                                            alt
                                        />
                                        <img v-else src="../assets/failure.svg" alt />
                                    </div>
                                    <div class="time-icon">
                                        <i class="el-icon-time"></i>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div class="center_c">
                        <div class="pricePublic_box">
                            <div class="seamless-warp">
                                <div class="seamless_top">
                                    <span>{{$t('main.priceList.price')}}</span>
                                    <span>{{$t('main.priceList.Amount')}}</span>
                                    <span>{{$t('main.priceList.time')}}</span>
                                </div>
                                <div class="scroll_box">
                                    <ul
                                        class="item scroll_tion"
                                        :style="{top:ListTop,transition:transition}"
                                        v-if="priceList.length"
                                    >
                                        <li
                                            v-for="(item,index) in priceList"
                                            :key="index"
                                            :style="{color: Number(item.type) === 1 ? '#e62250' : '#1dd086' }"
                                        >
                                            <span class="title" v-text="item.price"></span>
                                            <span class="amount">{{item.coinCount / 100}}</span>
                                            <span class="time">{{$date.formaMinuteTime(item.time)}}</span>
                                        </li>
                                    </ul>
                                    <div v-else class="NoPriceList">{{$t('main.priceList.NoData')}}</div>
                                </div>
                            </div>
                            <div class="scale-thermometer">
                                <div
                                    class="scale-structure scale-up"
                                    :style="{height: upPercentage * 240 + 'px'}"
                                ></div>
                                <div
                                    class="scale-structure scale-down"
                                    :style="{height: downPercentage * 240 + 'px'}"
                                ></div>
                            </div>
                        </div>
                        <div class="Ranking_box">
                            <pricePublic type="rank" :dataList="rankingList" />
                        </div>
                    </div>
                </div>

                <div class="center_r">
                    <div class="business-collection">
                        <el-tabs v-model="iconType" @tab-click="switchIconData">
                            <el-tab-pane :label="$t('main.Currency.Collection')" name="collection"></el-tab-pane>
                            <el-tab-pane :label="$t('main.Currency.commodity')" name="product"></el-tab-pane>
                            <el-tab-pane :label="$t('main.Currency.index')" name="index"></el-tab-pane>
                            <el-tab-pane :label="$t('main.Currency.stockMarket')" name="stock"></el-tab-pane>
                        </el-tabs>
                        <pricePublic
                            type="icon"
                            :dataList="iconDetailList"
                            @collectionCallback="getAllIconData"
                        />
                    </div>
                    <Detriment class="Detriment_box" @getOrderData="getOrderData" />
                </div>
            </div>
            <div class="clearfix"></div>
            <footer class="order-info-history">
                <el-tabs v-model="activeName3" type="card" @tab-click="handleClick">
                    <el-tab-pane :label="$t('main.TransactionRecord.Position')" name="Suspension">
                        <Suspension
                            :OrderData="OrderData.holdingList"
                            type="OpenPosition"
                            v-if="activeName3 == 'Suspension'"
                        />
                    </el-tab-pane>
                    <el-tab-pane
                        :label="$t('main.TransactionRecord.historyRecord')"
                        name="historyRecord"
                    >
                        <Suspension
                            :OrderData="OrderData.historyList"
                            type="history"
                            v-if="activeName3 == 'historyRecord'"
                        />
                    </el-tab-pane>
                </el-tabs>
            </footer>
            <!-- 交易结果 -->
            <div
                v-if="tradeResultStatus"
                class="modals-container-default"
                @click="tradeResultStatus = false"
            ></div>
            <div class="modal-container-trade-history" :class="{show: tradeResultStatus}">
                <div v-if="tradeResultStatus" class="head">
                    <h1>{{$t('main.OrderRecord.result')}}</h1>
                    <i class="close el-icon-close" @click="tradeResultStatus = false"></i>
                </div>
                <div v-if="tradeResultStatus" class="panes">
                    <div class="panes-holder">
                        <div class="panes-scroller" style="width: 480px;">
                            <div class="history-chart" style="width: 480px;">
                                <div class="view">
                                    <div class="deals-table">
                                        <div class="vertical-scroll" style="height: 100%;">
                                            <div class="inner-scroll-container">
                                                <div>
                                                    <div
                                                        class="row"
                                                        v-for="(item, index) in lastMinuteList"
                                                        :key="index"
                                                    >
                                                        <div class="first">
                                                            <div
                                                                class="major"
                                                            >{{item.tradeTime.split(' ')[1]}}
                                                                <span
                                                                    v-if="(item.tradeType).split('_')[3] == 'B'"
                                                                    style="color:#1DD086"
                                                                >{{$t('main.OrderRecord.up')}}</span>
                                                                <span
                                                                    v-else
                                                                    style="color:#E62250"
                                                                >{{$t('main.OrderRecord.down')}}</span>
                                                            </div>
                                                            <div
                                                                class="minor"
                                                            >{{item.tradeTime.split(' ')[0]}}</div>
                                                        </div>
                                                        <div class="second">
                                                            <div
                                                                class="major levels"
                                                                :style="{color: (item.marketPrice < item.remark) ? '#1DD086' : '#e62250'}"
                                                            >{{$t('main.OrderRecord.SettlementPrice')}}：{{item.remark}}</div>
                                                            <div
                                                                class="minor levels level-down"
                                                            >{{$t('main.OrderRecord.finalPrice')}}：{{item.marketPrice}}</div>
                                                        </div>
                                                        <div class="third">
                                                            <div class="major result green">
                                                                <span
                                                                    class="money undefined money-medium"
                                                                    :style="{color: (item.tradeType).split('_')[3] == 'B' ? '#1DD086' : '#E62250'}"
                                                                >{{item.settlePriceYuan}}</span>
                                                            </div>
                                                            <div class="minor">
                                                                <span
                                                                    class="money undefined money-medium"
                                                                >{{item.tradePriceYuan}}</span>
                                                            </div>
                                                        </div>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                                <div class="info">
                                    <div class="result-bar">
                                        <div class="item profit">
                                            <div class="title">{{$t('main.OrderRecord.profit')}}</div>
                                            <div class="value positive">
                                                <span
                                                    class="money undefined money-medium"
                                                >{{totalLastMinuteAmount.toFixed(2)}}</span>
                                            </div>
                                        </div>
                                    </div>
                                    <div class="side-button">
                                        <i></i>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <!-- 交易结果 -->
        </div>
    </div>
</template>

<script>
import BussinessChart from "@/components/bussinessChart";
import pricePublic from "@/components/pricePublic";
import Suspension from "@/components/suspension";
import Detriment from "@/components/detriment";
import { mapState } from "vuex";
let time;
export default {
    name: "transactionPage",
    data() {
        return {
            activeName: "first",
            iconType: "collection",
            activeName3: "Suspension",
            // 排行列表
            rankingList: [],
            // 价格列表
            priceList: [],
            transformIconAjaxData: {
                collection: [],
                product: [],
                index: [],
                stock: []
            },
            newsList: [
                {
                    title: "A simple, seamless scrolling for Vue.js"
                },
                {
                    title: "A curated list of awesome things related to Vue.js"
                }
            ],

            upPercentage: 0.8,
            downPercentage: 0.2,
            // 图表历史数据
            outcomeHistoryShowData: [],
            OrderData: [],
            // 交易结果蒙版
            tradeResultStatus: false,
            // 图表期数偏移量
            historyOffsetLeft: 0,
            // 图表期数图标的宽
            iconWidth: 22,
            // 屏幕宽
            sceenWidth: 0
        };
    },
    watch: {
        listData: function() {
            setTimeout(()=>{
                this.getRankData();
            },2000)
        }
    },
    computed: {
        ...mapState({
            // 币种列表
            iconDetailList: state => state.iconDetailList,
            lastMinuteList: state => state.lastMinuteList,
            chartData: state => state.chartData
        }),
        optionSingleHeight() {
            return {
                // limitMoveNum: 10,
                openWatch: true,
                singleHeight: 25
            };
        },
        optionLeft() {
            return {
                direction: 2,
                limitMoveNum: 1
            };
        },
        ListTop() {
            let lengths = this.priceList;
            let x = 11;
            let y = lengths.length - x;
            let top = -(y * 25) + 25;
            if (lengths.length >= x) {
                return top + "px";
            } else {
                return "25px";
            }
        },
        transition() {
            let lengths = this.priceList;
            if (lengths.length == 20) {
                this.priceList.splice(0, 10);
                return "all 0s";
            } else {
                return "all .5s";
            }
        },
        listData() {
            return this.$store.state.SuspensionEndTime;
        },
        totalLastMinuteAmount() {
            return this.lastMinuteList.reduce((cur, next) => {
                return Number(cur) + Number(next.settlePriceYuan);
            }, 0);
        }
    },
    components: {
        BussinessChart,
        pricePublic,
        Suspension,
        Detriment
    },
    created() {
        // 初始化所有的请求
        this.init();
        time = setInterval(() => {
            this.getPriceData();
        }, 2000);
    },
    mounted() {
        const updateElementSize = event => {
            if (!event || event.target.outerWidth < 901) {
                const results = document.getElementById("results-history");
                if (results) results.scrollLeft = 100000;
            }
        };
        window.onresize = event => {
            updateElementSize();
            this.setHistoryOffset();
        };
        window.onload = () => {
            updateElementSize();
        };
    },
    methods: {
        // 展示交易结果弹窗
        displayTradeResult(timeStamp) {
            setTimeout(async () => {
                // 每次list为空，防止登录失效后不调用订单列表，list一直存在长度导致弹窗不正常弹出
                this.$store.commit("setHoldingData", [])
                await this.getOrderData();
                if (this.lastMinuteList.length > 0) {
                    this.tradeResultStatus = true;
                } else {
                    this.tradeResultStatus = false;
                }
            }, 2000);

            // 每次交易结束的时候请求一个期数
            const time = this.outcomeHistoryShowData[
                this.outcomeHistoryShowData.length - 1
            ].time;
            let params = {
                code: this.chartData.code,
                market: this.chartData.market,
                prd: "1",
                time: time,
                count: 1
            };

            setTimeout(() => {
                this.getOutcomHistotyAjax(params).then(res => {
                    if (res) {
                        this.outcomeHistoryShowData = [
                            ...this.outcomeHistoryShowData,
                            ...res.data.data
                        ];
                        this.setHistoryOffset();
                    }
                });
            }, 1000);
        },
        // 设置图表偏移
        setHistoryOffset() {
            // 每次根据屏幕设置不同的图表宽度
            this.sceenWidth = document.body.clientWidth;
            this.$store.commit("setSceenWidth", this.sceenWidth);
            if (this.sceenWidth <= 768) {
                this.iconWidth = 19;
            } else {
                this.iconWidth = 22;
            }
            const historyDom = document.getElementById("results-history");
            const chartContainerDom = document.getElementById(
                "chart-container"
            );
            // 获取期数外层dom宽，每次根据外层百分之九十的宽去计算多少个
            let chartContainerDomWidth = chartContainerDom.clientWidth;
            const totalCount = Math.round(
                (chartContainerDomWidth * 0.9) / this.iconWidth
            );
            // 将宽度设置成整数
            historyDom.style.width = this.iconWidth * totalCount + "px";
            // 超出宽度个数时，设置便宜
            if (this.outcomeHistoryShowData.length > totalCount) {
                if (this.sceenWidth <= 768) {
                    this.historyOffsetLeft =
                        (this.outcomeHistoryShowData.length - totalCount + 1) *
                        this.iconWidth;
                } else {
                    this.historyOffsetLeft =
                        (this.outcomeHistoryShowData.length - totalCount) *
                        this.iconWidth;
                }
            } else {
                this.historyOffsetLeft = 0;
            }
        },
        getOrderData() {
            let token = this.$common.getCookie("token");
            if (!token) {
                return false;
            }
            return new Promise((resolve, reject) => {
                this.$http.getOrderData().then(res => {
                    if (res) {
                        let arr = res.data.data;
                        arr.holdingList.reverse();
                        arr.historyList.reverse();
                        this.OrderData = arr;
                        this.$store.commit(
                            "setHoldingData",
                            res.data.data.lastMinuteList
                        );
                        resolve(arr);
                    }
                });
            });
        },
        init() {
            // 获取币种数据
            this.getAllIconData();
            // 获取价格模块数据
            this.getPriceData();
            // 获取排行模块数据
            this.getRankData();
            // 获取图表期数数据
            this.getOutcomHistoty();
            //获取购买订单数据
            this.getOrderData();
        },
        getPriceData() {
            this.$http.getTradeVolume().then(res => {
                if (res) {
                    let x =
                        res.data.data.proportion == 0
                            ? 50
                            : res.data.data.proportion;
                    this.upPercentage = Number(x) / 100;
                    this.downPercentage = (100 - Number(x)) / 100;
                    this.priceList = [
                        ...this.priceList,
                        ...res.data.data.trade
                    ];
                }
            });
        },
        getRankData() {
            this.$http.getTradeRank().then(res => {
                if (res) {
                    this.rankingList = res.data.data;
                    this.rankingList.sort((a,b) => {
                        return b['profit'] - a['profit']
                    })
                }
            });
        },
        getAllIconData() {
            this.$http.currencyForm().then(res => {
                if (res) {
                    res.data.data.forEach(v => {
                        v.collectionStatus = 0;
                    });
                    let result = res.data.data;
                    this.transformIconAjaxData.product = result.filter(
                        v => v.market === "fx"
                    );
                    this.transformIconAjaxData.index = result.filter(
                        v => v.market === "1"
                    );
                    this.transformIconAjaxData.stock = result.filter(
                        v => v.market === "2"
                    );

                    this.getCollectionData(result);
                } else {
                }
            });
        },
        async getOutcomHistoty() {
            if (!this.chartData.code && !this.chartData.market) {
                setTimeout(() => {
                    this.getOutcomHistoty();
                }, 1000);
                return false;
            }
            const params = {
                code: this.chartData.code,
                market: this.chartData.market,
                prd: "1"
            };
            this.getOutcomHistotyAjax(params).then(res => {
                if (res) {
                    this.outcomeHistoryShowData = res.data.data;
                    this.setHistoryOffset();
                }
            });
        },
        getOutcomHistotyAjax(params) {
            return this.$http.getPreviousData(params);
        },
        transformHistoryData(data, count = 5) {
            //data:数据  count一列放几个，默认5个
            // 连续的涨或跌都归入一个数组
            // 一个数组最多放5个值
            let resultMap = [];
            let index = 0;
            data.forEach((v, i) => {
                if (!resultMap[index]) {
                    resultMap[index] = [];
                    resultMap[index].push(v);
                }

                let arr = resultMap[index];
                if (!(arr[arr.length - 1] === v && arr.length < count)) {
                    index++;
                    resultMap[index] = [];
                }

                resultMap[index].push(v);
            });

            return resultMap;
        },
        // 切换货币类型
        switchIconData() {
            this.$store.dispatch(
                "setIconDetailList",
                this.transformIconAjaxData[this.iconType]
            );
            this.$store.dispatch(
                "setCloneIconDetailList",
                this.transformIconAjaxData[this.iconType]
            );
        },
        // 获取收藏币种
        getCollectionData(allIconData) {
            let token = this.$common.getCookie("token");
            if (!token) {
                this.switchIconData();
                return false;
            }
            this.$http.userDetails().then(res => {
                if (res) {
                    let collectionList = res.data.data.favorite;
                    let markets = collectionList.map(v => v.market);
                    allIconData.forEach(item => {
                        if (markets.length && markets.includes(item.market)) {
                            item.collectionStatus = 1;
                        }
                    });
                    this.transformIconAjaxData.collection = allIconData.filter(
                        v => v.collectionStatus === 1
                    );
                    this.switchIconData();
                }
            });
        },
        handleClick() {}
    },
    destroyed() {
        clearInterval(time);
    }
};
</script>

<style  lang="scss">
.transactionPage {
    .seamless_top {
        position: absolute;
        height: 25px;
        top: 0;
        background: #191c25;
        color: #ffffff;
        z-index: 999;
        width: calc(100% - 10px);
        font-size: 12px;
        display: flex;
        align-items: center;
        padding-right: 10px;
        span {
            flex: 1;
            text-align: center;
        }
    }
    .seamless-warp2 {
        overflow: hidden;
        height: 25px;
        width: 100%;
        div {
            height: 25px !important;
        }
        ul.item {
            width: 100%;
            li {
                float: left;
                line-height: 25px;
                margin-right: 10px;
            }
        }
    }
    .scroll_box {
        height: 300px;
        overflow: hidden;
        position: relative;
        .transitionName {
            transition: all 0.5s;
        }
        .scroll_tion {
            position: absolute;
            width: 100%;
            top: 25px;
            li {
                line-height: 25px;
                height: 25px;
                color: #fff;
                display: flex;
                font-size: 12px;
                justify-content: space-between;
                span {
                    flex: 1;
                    text-align: center;
                }
            }
        }
        .NoPriceList {
            height: 100%;
            margin-top: 50px;
            text-align: center;
            font-size: 12px;
            color: #909399;
        }
    }
    .mobile-show {
        display: none;
    }
    .main-content {
        width: 90%;
        margin: auto;
    }
    .nameIndex {
        padding: 10px 0 5px 10px;
        color: #dddee5;
        line-height: 20px;
        font-size: 13px;

        .current-price {
            display: inline-block;
            vertical-align: middle;
            margin-left: 25px;

            .current-price-label {
                font-size: 11px;
            }

            .current-price-amount {
                font-weight: bold;
            }
        }
    }

    .center_l_t {
        display: inline-block;
        vertical-align: bottom;
        margin-left: 70px;

        .tab_box {
            font-size: 12px;
            display: flex;
            color: #787b89;

            .el-tabs__header {
                margin: 0;
                border: none;
            }

            .el-tabs__nav {
                border: none;
            }

            .el-tabs__item {
                color: #787b89;
                font-size: 12px;
                border: none;
                padding: 0 10px;
                @media screen and (max-width: 1100px) {
                    padding: 0 10px !important;
                }
                &.is-active {
                    color: #04affe;
                }
            }
        }

        .tab_box_r {
            display: flex;
            p {
                line-height: 40px;
                margin-left: 25px;
            }

            .direction-symbol {
                position: relative;
                bottom: 1px;
                font-size: 7px;
                line-height: 12px;
            }

            @media screen and (max-width: 1100px) {
                p {
                    margin-left: 20px;
                }
            }
        }
    }

    .center_box {
        height: 600px;
        display: flex;
        flex-direction: row;

        .el-tabs__nav,
        .el-tabs__item {
            border: none !important;
        }
        .desktop-reverse {
            display: flex;
            flex-direction: row-reverse;
            flex: 5;

            .center_l {
                flex-grow: 4;

                .results-description {
                    color: #dddee5;
                    font-size: 12px;
                    padding: 10px 0 0 4px;
                }

                .results-history {
                    padding: 0;
                    margin-left: 4px;
                    display: flex;
                    width: 90%;
                    overflow: hidden;

                    .results-history-column {
                        display: flex;
                        height: auto;
                        width: auto;
                        margin-top: 8px;
                        vertical-align: top;
                        padding: 0 4px 0 0;
                    }
                    img {
                        width: 18px;
                    }
                    @media screen and (min-width: 960px) {
                        .el-icon-time {
                            font-size: 20px;
                            position: absolute;
                            right: -20px;
                            top: 8px;
                            color: #fff;
                        }
                    }
                }
            }
            .center_c {
                max-width: 310px;
                min-width: 270px;
                flex-grow: 1;
                height: 100%;
            }
        }

        .center_r {
            flex: 1;
            height: 100%;
            max-width: 330px;

            .business-collection {
                .el-tabs__header {
                    margin: 0;
                    border-width: 1px;

                    .el-tabs__item {
                        padding: 0 10px;
                        font-size: 12px;
                        color: #485371;
                        height: 33px;

                        &.is-active {
                            color: #409eff;
                        }
                    }
                }

                .el-tabs__active-bar.is-top {
                    height: 1px !important;
                    margin-bottom: 2px;
                }
                .el-tabs__nav-wrap::after {
                    display: none !important;
                }
            }
        }

        .pricePublic_box,
        .business-collection,
        .Ranking_box,
        .Detriment_box {
            height: 50%;
            overflow: hidden;
        }

        .pricePublic_box {
            border-top: 1px solid #787b89;
            border-left: 1px solid #787b89;
            position: relative;
            padding-right: 10px;
            .scale-thermometer {
                width: 9px;
                position: absolute;
                right: 16px;
                top: 40px;
                z-index: 10;
                .scale-structure {
                    width: 100%;
                }
                .scale-up {
                    background: #1dd086;
                    border-top-left-radius: 23px;
                    border-top-right-radius: 23px;
                }
                .scale-down {
                    background: #e62250;
                    border-bottom-right-radius: 23px;
                    border-bottom-left-radius: 23px;
                }
            }
        }

        .business-collection {
            border-top: 1px solid #787b89;
            border-right: 1px solid #787b89;
        }

        .Ranking_box {
            border-left: 1px solid #787b89;
            border-bottom: 1px solid #787b89;
        }

        .Detriment_box {
            border-right: 1px solid #787b89;
            border-bottom: 1px solid #787b89;
        }

        .chart-container {
            border: 1px solid #787b89;
            height: 100%;
        }
    }

    .order-info-history {
        border: 1px solid #787b89;
        border-top: none;
        .el-tabs {
            .el-tabs__nav {
                border: none;
            }
            .el-tabs__header {
                margin-bottom: 0;
                border-bottom: 1px solid #787b89;
            }
            .el-tabs__item {
                color: #dddee5;
                border: none;
                font-size: 12px;

                &.is-active {
                    color: #04affe;
                    border-bottom: 1px solid #04affe !important;
                }
            }
        }
    }

    @media screen and (max-width: 900px) {
        .clearfix::after {
            content: "";
            clear: both;
            display: table;
        }

        .main-content {
            margin: 0;
            width: 100%;
            border-bottom: 1px solid #787b89;
        }

        .center_l_t {
            .el-tabs {
                display: none;
            }
        }
        .center_box {
            display: block !important;
            .desktop-reverse {
                display: block !important;
            }
            .chart-container {
                border-bottom: none;
            }
            .center_l {
                display: block !important;
            }

            .fade-in {
                position: absolute;
                float: right;
                z-index: 1;
                top: 0;
                left: 0;
                pointer-events: none;
                background-image: linear-gradient(
                    to left,
                    rgba(255, 255, 255, 0),
                    #202432 100%
                );
                width: 60px;
                height: 25px;
            }

            .results-history {
                max-width: calc(100vw - 20px);
                overflow: hidden;
                display: flex;
                position: relative;

                .results-history-column {
                    display: flex !important;
                    width: auto !important;
                    height: auto !important;
                    img {
                        width: 15px !important;
                    }
                }
                .el-icon-time {
                    color: #787b89;
                    margin-top: 8px;
                    font-size: 17px;
                    display: block !important;
                }
            }

            .center_c {
                float: left;
                min-width: 50%;
                max-width: 50%;
                height: 300px !important;
                .pricePublic_box {
                    border-top: none;
                    .scale-thermometer {
                        display: none;
                    }
                }
            }

            .center_r {
                float: right;
                min-width: 50%;
                max-width: 50%;
                height: 300px !important;

                .business-collection {
                    display: none;
                }
                .Detriment {
                    height: 100%;
                }
            }

            .Ranking_box {
                border-bottom: none;
            }

            .Detriment_box {
                border-bottom: none;
            }
        }
    }

    @media screen and (max-width: 768px) {
        .mobile-show {
            display: block;
        }
        .nameIndex {
            font-size: 14px;
            padding-left: 10px;
            padding-right: 0;
            display: flex;
            justify-content: space-between;

            .current-price {
                vertical-align: top;
                margin-left: 10px;
                .hour-change {
                    color: #787b89;
                    font-size: 12px;
                }
            }
            .current-price-amount {
                font-size: 22px;
            }
            .mobile-hide {
                display: none;
            }
            .volume-up-down {
                padding-right: 15px;
                .volume {
                    color: #a8aba9;
                }
                table {
                    color: #787b89;
                    font-size: 12px;
                    line-height: 12px;
                    td {
                        padding-bottom: 3px;
                        padding-right: 9px;
                    }
                }
            }
        }

        #chart-box {
            height: 250px;
        }

        .center_l_t {
            margin-left: 20px;
            .tab_box_r {
                flex-direction: column;
                line-height: 25px;

                p {
                    line-height: 20px;
                }
            }
        }

        .center_c {
            float: left;
            min-width: 49% !important;
            max-width: 49% !important;
        }

        .center_r {
            float: right;
            min-width: 50% !important;
            max-width: 50% !important;
        }

        .order-info-history {
            display: none;
        }
    }
}
</style>

