import Vue from 'vue'
import VueRouter from 'vue-router'
import Index from '../views/index.vue'
import transactionPage from '../views/transactionPage.vue'
import HomeSection from '../views/home/homeSection.vue'
import login from '../views/login/login.vue'
import register from '../views/register/register.vue'
import ordersPage from '../views/ordersPage.vue'
import AccountPage from '../views/user/accountPage.vue'
// 财务页面
import AssetsIndexPage from '../views/assets/index.vue'
import AssetsPage from '../views/assets/assets.vue'
import HistoryPage from '../views/assets/history.vue'
import DepositPage from '../views/assets/deposit.vue'
import WithdrawPage from '../views/assets/withdraw.vue'

Vue.use(VueRouter)

const routes = [
    {
        path: '/',
        component: Index,
        children: [
            { path: '/', name: 'homeSection', component: HomeSection },
            { path: '/transactionPage', name: 'transactionPage', component: transactionPage },
            { path: '/orders', name: 'orders', component: ordersPage },
            {
                path: '/assets',
                component: AssetsIndexPage,
                children: [
                    { path: '/', name: 'assets', component: AssetsPage },
                    { path: '/assets/history', name: 'history', component: HistoryPage },
                    { path: '/assets/deposit', name: 'deposit', component: DepositPage },
                    { path: '/assets/withdraw', name: 'withdraw', component: WithdrawPage }
                ]
            },
            { path: '/account', name: 'account', component: AccountPage },
        ]
    },
    { path: '/login', name: 'login', component: login },
    { path: '/register', name: 'register', component: register },
]

const router = new VueRouter({
    routes
})

export default router